div.index div.part.product div.cont div.tp div.item a div.img{margin-right:50px;width:44%;
}
div.index div.part.product div.cont div.tp div.item a div.text{margin-left:40px;width:40%;
}
div.index div.part.product div.cont div.tp div.item a div.text p{font-size:14px;
}
div.index div.part.product div.cont div.tp div.item a div.text p.tit{font-size:20px;
}
div.index div.part.product div.cont div.btm div.item a{padding:20px 20px 0 20px;
}
div.index div.part.product div.cont div.btm div.item a i.iconfont{width:16%;font-size:20px;
}
div.index div.part.product div.cont div.btm div.item a div.txt p{font-size:12px;
}
div.index div.part.product div.cont div.btm div.item a div.txt p.tit{font-size:14px;
}
div.index div.part.client div.ad{-webkit-transform:translateY(0);transform:translateY(0);-ms-transform:translateY(0);
}
div.index div.part.client div.ad div.msg{padding-top:50px;
}
div.index div.part.client div.ad div.msg p{font-size:16px;line-height:40px;
}
div.index div.part.client div.ad div.msg p:fistt-line{font-size:24px;
}
}
@media screen and (max-width:800px){
div.index div.banner div.text div.tit h4{font-size:26px;line-height:50px;
}
div.index div.banner div.text div.tit span.line{margin-top:15px;margin-right:20px;margin-left:20px;height:20px;
}
div.index div.banner div.text div.tit div.tips p{margin:auto;font-size:14px;line-height:25px;
}
div.index div.banner div.text div.en{margin-top:22px;font-size:30px;
}
div.index div.part.solution div.cont div.swiper-container div.swiper-wrapper div.item a div.txt{padding:20px 10px 0 10px;
}
div.index div.part.solution div.cont div.swiper-container div.swiper-wrapper div.item a div.txt p.tit{margin-top:10px;margin-bottom:10px;
}
}
@media screen and (max-width:767px){
div.index div.banner div.bg{text-align:center;
}
div.index div.banner div.text{padding-right:1.5rem;padding-left:1.5rem;width:100%;
}
div.index div.banner div.text div.en{display:none;
}
div.index div.banner div.text div.tit h4{display:block;font-weight:400;font-size:2.2rem;line-height:4rem;
}
div.index div.banner div.text div.tit span.line{display:none;
}
div.index div.banner div.text div.tit div.tips{display:block;width:100%;
}
div.index div.banner div.text div.tit div.tips p span{display:inline-block;margin-right:.7rem;margin-left:.7rem;text-align:center;font-size:1.3rem;
}
div.index div.banner span.music{right:2rem;
}
div.index div.part div.title{padding-top:1rem;padding-bottom:1.5rem;-webkit-transform:translateY(0);transform:translateY(0);-ms-transform:translateY(0);
}
div.index div.part div.title p{font-size:1.7rem;
}
div.index div.part div.title p.desc{margin-top:1.5rem;font-size:.9rem;
}
div.index div.part div.cont div.more{margin-top:2.3rem;margin-bottom:3rem;width:10rem;height:2.7rem;
}
div.index div.part div.cont div.more a{font-size:1rem;
}
div.index div.part.solution div.cont{-webkit-transform:translateY(0);transform:translateY(0);-ms-transform:translateY(0);
}
div.index div.part.solution div.cont div.swiper-container div.btn{top:56%;width:3rem;height:3rem;
}
div.index div.part.solution div.cont div.swiper-container div.btn.prev{left:.75rem;
}
div.index div.part.solution div.cont div.swiper-container div.btn.next{right:.75rem;
}
div.index div.part.solution div.cont div.swiper-container div.btn:before{width:100%;height:100%;
}
div.index div.part.solution div.cont div.more{margin-top:2.3rem;margin-bottom:3rem;width:10rem;height:2.7rem;
}
div.index div.part.solution div.cont div.more a{font-size:1rem;line-height:2.7rem;
}
div.index div.part.client div.ad{-webkit-transform:translateY(0);transform:translateY(0);-ms-transform:translateY(0);
}
div.index div.part.client div.ad div.msg{padding-top:2.5rem;padding-right:1.5rem;padding-left:1.5rem;
}
div.index div.part.client div.ad div.msg span{margin-bottom:0;
}
div.index div.part.client div.ad div.msg p{font-size:1.2rem;line-height:2.5rem;
}
div.index div.part.client div.ad div.msg p:first-line{font-size:1.8rem;line-height:4.5rem;
}
}
div.case div.case_list{margin:0 auto;width:67.5rem;
}
div.case div.case_list div.title ul li{float:left;display:inline-block;margin-bottom:2.5rem;width:12.5%;height:2.8rem;border-radius:5px;color:#333;text-align:center;font-size:20px;line-height:2rem;cursor:default;
}
div.case div.case_list div.title ul li span{display:inline-block;width:100%;border-left:1px solid #979899;vertical-align:middle;line-height:1;
}
div.case div.case_list div.title ul li:nth-child(10n+10) span{border-right:1px solid #979899;
}
div.case div.case_list div.title ul li.active{background:#0078f0;color:#fff;
}
div.case div.case_list div.title ul li.active span{border:none;
}
div.case div.case_list div.title ul li.active+li span{border:none;
}
div.case div.case_list div.title ul.long li{width:12.5%;
}
div.case div.case_list div.title ul.long li:nth-child(10n+10) span{border-right:none;
}
div.case div.case_list div.title ul.long li:nth-child(8n+8) span{border-right:1px solid #979899;
}
div.case div.case_list div.cont{padding-bottom:2.5rem;
}
div.case div.case_list div.cont ul.list{border-top:1px solid #e3e4e6;border-left:1px solid #e3e4e6;
}
div.case div.case_list div.cont ul.list li{float:left;width:16.66%;height:7.5rem;border-right:1px solid #e3e4e6;border-bottom:1px solid #e3e4e6;background:no-repeat center center;background-size:80%;line-height:7.5rem;
}
div.case div.case_list div.cont ul.list li:nth-child(6n+6){border-right:1px solid #e3e4e6;
}
div.case div.case_list div.cont ul.list li a{display:block;width:100%;height:100%;
}
@media screen and (max-width:767px){
div.case div.case_list{overflow:hidden;margin:0 auto;width:100%;
}
div.case div.case_list div.title1{margin-bottom:2rem;border-bottom:1px solid #e3e4e6;line-height:4.8rem;-webkit-tap-highlight-color:transparent;
}
div.case div.case_list div.title1 div div{text-align:center;font-size:16px;
}
div.case div.case_list div.title1 div div:after{float:right;color:#333;content:'|';
}
div.case div.case_list div.title1 div div.swiper-slide-active{color:#0078f0;
}
div.case div.case_list div.title1 div.active{color:#0078f0!important;
}
div.case div.case_list div.cont{margin:0 auto;padding-right:0;padding-bottom:2.5rem;padding-left:0;width:90.625%;
}
div.case div.case_list div.cont ul.list{width:100%;
}
div.case div.case_list div.cont ul.list li{width:33.33333333%;height:6.4rem;line-height:6.4rem;
}
}






